<template>
    <div class="chooseAddress_container">
        <heade-top head-title="选择地址" goBack="true" ></heade-top>
        <router-link to="/confirmOrder/chooseAddress/addAddress" class="add_icon_footer">
            <img src="../../../images/add_address.png" alt="" width="24" height="24">
            <span>新增收货地址</span>
        </router-link>
        <section class="scroll_container">
            <section class="list_cotainer">
                <ul class="deliverable_address">
                    <!-- @click.prevent.stop阻止事件冒泡 -->
                    <li v-for="(item,index) in deliverable" :key="index" @click.prevent.stop="chooseAddress(item,index)">
                        <svg class="choosed_address" :class="{default_address: defaultIndex == index}">
                            <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#select"></use>
                        </svg>
                        <div>
                            <header>
                                <span>{{item.name}}</span>
                                <span>{{item.sex==1?'先生' : '女士'}}</span>
                                <span>{{item.phone}}</span>
                            </header>
                            <div class="address_detail ellipsis">
                                <span v-if="item.tag">{{item.tag}}</span>
                                <p>{{item.address_detail}}</p>
                            </div>
                        </div>
                    </li>
                </ul>
                <section v-if="deliverdisable.length" id="out_delivery">
                    <header class="out_header">一下地址超出配送范围</header>
                    <ul>
                        <li v-for="(item,index) in deliverdisable" :key="index">
                             <svg class="choosed_address">
                                <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#select"></use>
                            </svg>
                            <div>
                                <header>
                                    <span>{{item.name}}</span>
                                    <span>{{item.sex == 1? '先生' : '女士'}}</span>
                                    <span>{{item.phone}}</span>   
                                </header>
                                <div class="address_detail ellipsis">
                                    <span v-if="item.tag" :style="{backgroundColor: '#ccc', color: '#fff'}">{{item.tag}}</span>
                                    <p>{{item.address_detail}}</p>   
                                </div>
                            </div>
                        </li>
                    </ul>
                </section>
            </section>
        </section>
        <!-- mode="out-in"当前元素先进行过度完成后新元素过度进入 -->
        <transition name="addAddress_rout" mode="out-in">
            <router-view></router-view>
        </transition>
    </div>
</template>

<script>
import headeTop from '../../../components/header/header'
import {mapState, mapMutations} from 'vuex'
import {getAddress, getAddressList} from '../../../service/getData'
import alertTip from '../../../components/common/alertTip'
    import BScroll from 'better-scroll'
export default {
    created () {
        this.id=this.$route.query.id;
        this.sig=this.$route.query.sig;
    },mounted () {
        this.initDate();
    }
    ,
    components: {
        headeTop,
        alertTip
    },data(){
        return{
            addressList:[],//地址列表
            deliverable:[],//有地址列表
            deliverdisable:[],//无效地址列表
            id:null,//Id下单编号
            sig:null,
            showAlert:false,
            alertText:null
        }
    },computed: {
        ...mapState([
            'userInfo','addressIndex','newAddress'
        ]),
        //选择收货地址
        defaultIndex()
        {
            if(this.addressIndex)
            {
                return this.addressIndex;
            }else{
                return 0;
            }
        }
    },methods: {
        ...mapMutations([
            'CHOOSE_ADDRESS'
        ]),
        //初始化信息
        async initDate()
        {
            if(this.userInfo && this.userInfo.user_id)
            {
                this.addressList=[];
                this.deliverable=[];
                this.deliverdisable=[];
                this.addressList=await getAddressList(this.userInfo.user_id);
                //地址分为有效和无效两种
                this.addressList.forEach(item=>{
                    if(item.is_deliverable)
                    {
                        this.deliverable.push(item);
                    }else{
                        this.deliverdisable.push(item);
                    }
                })
            }
        },
        //选择地址
        chooseAddress(addres,index)
        {
            //将选择的地址存入vuex
            
            this.CHOOSE_ADDRESS({addres,index});
            
            this.$router.go(-1);
        }
    },
    watch:{
        userInfo:function(value)
        {
            if(value && value.user_id)
            {
                this.initDate();
            }
        },
        newAddress:function(value)
        {
            this.initDate();
        }
    }
}
</script>

<style lang="scss" scoped>
@import '../../../style/mixin.scss';
.chooseAddress_container{
    position:fixed;
    top:0;
    left:0;
    right: 0;
    bottom:0;
    z-index: 202;
    background: #fff;
}
.add_icon_footer{
    display: flex;
    position: absolute;
    bottom:0;
    left:0;
    right: 0;
    align-items: center;
    justify-content: center;
    height: 2.5rem;
    z-index: 204;
    span{
        @include sc(0.7rem,$blue);
        margin-left:0.3rem;
    }
}   
    .addAddress_rout-enter-active, .addAddress_rout-leave-active {
        transition: all .4s;
    }
    .addAddress_rout-enter, .addAddress_rout-leave-active {
        transform: translate3d(2rem, 0, 0);
        opacity: 0;
    }
.scroll_container{
    position: fixed;
    top:0;
    left:0;
    right:0;
    bottom:0;
    padding-top:1.95rem;
    overflow-y: auto;
}
.list_cotainer{
    padding-bottom: 5rem;
}
.deliverable_address{
    background: #fff;
        li{
            border-bottom:0.025rem solid #f1f1f1;
            padding:0.6rem;
            align-items: center;
            display: flex;
            .choosed_address{
                opacity: 0;
               margin-right: 0.5rem;
                @include wh(.8rem,.8rem);
            }
            .default_address{
                opacity: 1;
                fill: #4cd964;
            }
            header{
                span:nth-of-type(1)
                {
                    font-weight: bold;
                    @include sc(0.7rem,#666);
                }
                span:nth-of-type(3)
                {
                    font-weight: bold;
                }
                @include sc(0.6rem,#666);
            }
            .address_detail{
                margin-top:0.3rem;
                display: flex;
                align-items: center;
                span:nth-of-type(1)
                {
                    @include sc(0.5rem,#fff);
                    //padding:0.1rem;
                    background-color: #ff5722;
                    border-radius: 0.8rem;
                    height: 0.7rem;
                    width: 1.5rem;
                    text-align: center;
                }
                p{
                    padding-left:0.3rem;
                    @include sc(0.5rem,#666);
                }
            }
        }
}
</style>



